.card--movie {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  border-radius: 10px;
  background-color: #fff;
  -webkit-box-shadow: 0px 10px 13px -7px var(--font-color),
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow: 0px 10px 13px -7px var(--font-color),
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  animation-duration: 1s;
  animation-fill-mode: forwards;
  visibility: hidden;
}

.card-content {
  margin-left: 10px;
}
