.card--nomination {
  height: 150px;
  align-items: center;
  width: 90%;
  max-width: 600px;
  margin: 20px 0;
  animation-duration: 1s;
  visibility: hidden;
  animation-fill-mode: forwards;
  grid-template-rows: auto;
  min-width: 0;
  min-height: 0;
  color: var(--font-color);
  display: grid;
  grid-template-columns: 50px 100px 1fr;
  -webkit-box-shadow: 0px 10px 13px -7px var(--font-color),
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow: 0px 10px 13px -7px var(--font-color),
    5px 5px 15px 5px rgba(0, 0, 0, 0);
  align-content: center;
  border-radius: 10px;
}
@media screen and (max-width: 1000px) {
  .card--nomination {
    right: 0;
    max-width: none;
    width: 90%;
    margin: 30px 0;
  }
}
