.button--remove {
  transition: 0.2s all ease;
  margin-right: 10px;
  background-color: var(--font-color);
  border-radius: 50%;
  height: 40px;
  flex: 0 0 40px;
  color: white;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-self: center;
  align-self: center;
}
.button--remove:hover {
  opacity: 0.7;
  transform: scale(1.05);
}

@media screen and (max-width: 1000px) {
  .button--remove {
    margin-right: 20px;
  }
}
