.button--add {
  background-color: var(--font-color);
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  cursor: pointer;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  margin-right: 10px;
  transition: 0.3s ease;
}
.button--add:hover {
  transform: scale(1.05);
  opacity: 0.7;
}
.button--add.disabled {
  background-color: grey;
  cursor: default;
  pointer-events: none;
}
