:root {
  --bg-primary: #52c7db;
  --font-color: #030f45;
  --emphasis-color: #245764;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Archivo", sans-serif;
  font-weight: 600;
}
.App {
  text-align: center;
  transition: 0.1s ease;
  height: 100vh;
  min-height: 100%;
  overflow: auto;
  animation: hide-scroll 0.7s backwards;
}

body {
  background-color: var(--bg-primary) !important;
  color: var(--font-color);
}
.column-container {
  display: grid;
  grid-template-columns: 55% 1fr;
  padding-left: 5%;
  height: 100%;
}
.column-container > div {
  height: 100%;
}
.column--left {
  max-width: 450px;
  height: 100%;
  text-align: left;
}
.column--right {
  background-color: #fff;
  height: 100%;
  animation-duration: 0.5s;
  flex-direction: column;
  display: flex;
  align-items: center;
}

@media screen and (max-width: 1000px) {
  .column-container {
    grid-template-columns: 1fr;
    padding: 10px 0 0 0;
  }
  .column-container > div {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .column--left {
    margin: 0 auto;
    max-width: none;
    width: 90%;
    align-items: center !important;
  }
}

@keyframes hide-scroll {
  from,
  to {
    overflow: hidden;
  }
}
